       html, body {
        margin: 0;
        padding: 0;
      }
      
      body {
        font-family: 'Roboto', sans-serif;
        font-weight: 100;
      }
      
      .container {
        margin: 0 auto;   
        padding: 0 10px;
      }
      
      
      .header {
        background: black;
        overflow: hidden;
        background: #C33764;
        background: -webkit-linear-gradient(rgba(29, 38, 113, 0.4), rgba(117, 74, 88, 0.8)), url(https://images.unsplash.com/photo-1483389127117-b6a2102724ae?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjF9);
        background-size: cover;
        height: 800px;
        text-align: center; 
      }
      
      
      .header .container {
        position: relative;
        top: 200px;
      }
      
      .header h1 {
        color: #fff;
        line-height: 100px; 
        font-size: 80px;
        margin-top: 0;
        margin-bottom: 80px;
        text-transform: uppercase; 
      }
      
      @media (min-width:850px) {
        .header h1 {
          font-size: 120px;
        }
      }
      
      .header p {
        color: #fff;
        font-weight: 500;
        letter-spacing: 8px;
        margin-bottom: 40px;
        margin-top: 0;
        text-transform: uppercase; 
      }
      
      .btn {
        color: #fff;
        background: #000;
        padding: 10px 40px;
        text-decoration: none; 
        transition: background .5s; 
      }
      
      .nav { 
        background: #000;
        height: 80px; 
        width: 100%;
      }
      
      .nav ul {
        height: 80px;
        list-style: none;
        margin: 0 auto; 
        padding: 0;
      }
      
      .nav ul li {
        color: #fff;
        display: inline-block; 
        height: 80px;
        line-height: 80px; 
        list-style: none;
        padding: 0 10px;
        transition: background .5s; 
      }
      
      .btn:hover, .nav ul li:hover {
        background: #117bff;
        cursor: pointer; 
        transition: background .5s;  
      }
      
      .main .container {
        margin: 80px auto;
      }
      
      .main img {
        float: left;
        margin: 50px 80px 50px 0;
        border-radius: 30px;
      }
      
      .jumbotron {
        background: black;
        background: -webkit-linear-gradient(rgba(148, 0, 211, 0.2), rgba(0, 0, 0, 1)), url(https://images.unsplash.com/photo-1597892657493-6847b9640bac?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjF9);
        background-size: cover;
        height: 600px;
      }
      
      .jumbotron .container {
        position: relative;
        top: 220px;
      }
      
      .jumbotron h2 {
        color: #fff;
        text-align: right; 
      }
      
      .jumbotron p {
        color: #fff; 
        text-align: right; 
      }
      
      .jumbotron .btn {
        margin: 10px 0 0;
        float: right; 
      }
      
      .footer { 
        background: #000;
        height: 80px; 
        padding-bottom: 50px;
      }
      
      .footer p { 
        color: #fff;
        font-size: 14px;  
        height: 80px; 
        line-height: 80px;
        margin: 0;  
      }
      
      @media (max-width: 500px) {
        .header h1 {
          font-size: 50px;
          line-height: 64px;
        }
      
        .main, .jumbotron {
          padding: 0 30px;
        }
      
        .main img {
          width: 100%;
          border-radius: 30px;
        }
        
    }